<template>
  <div class="setPosition">
      <div class="setTop">
        <span>当前位置:
          <i>设置</i>
        </span>
      </div>
    <div class="setContent clearfix">
      <div class="setLeft">
        <div>
          <h1>个人信息</h1>
        </div>
        <div>
          姓名:<input type="text" v-model="perInfor.name" readonly>
        </div>
        <div>
            所属地区:<input type="text" v-model="perInfor.section_one" readonly>
        </div>
        <div>
            所属部门:<input type="text" v-model="perInfor.section_two" readonly>
        </div>
        <div>
          <h2>修改密码</h2>
        </div>
        <div>
            旧密码:<input type="password" v-model="oldPwd">
        </div>
        <div>
            新密码:<input type="password" v-model="newPwd">
        </div>
          <button @click="changePwd()">保存修改</button>
      </div>
      <div class="setRight">
        <div class="topImg">
          <!--<el-upload-->
            <!--class="avatar-uploader"-->
            <!--action="https://jsonplaceholder.typicode.com/posts/"-->
            <!--:show-file-list="false"-->
            <!--:on-success="handleAvatarSuccess"-->
            <!--:before-upload="beforeAvatarUpload">-->
            <!--<img v-if="imageUrl" :src="imageUrl" class="avatar">-->
            <!--<i v-else class="el-icon-plus avatar-uploader-icon"></i>-->
          <!--</el-upload>-->
            <img src="../../assets/avatar.jpg" alt="">
          <p>{{perInfor.name}}</p>
        </div>
        <!--<div class="submit">-->
          <!--<button @click="handleAvatarSuccess">上传头像</button>-->
        <!--</div>-->
      </div>
    </div>
  </div>

</template>
<script>
  export default {
    data(){
      return{
        imageUrl: '',
//        name:" ",
        id:'',
        oldPwd:'',//旧密码
        newPwd:'',//新密码
        perInfor:{
          name:'',
          section_one:'',
          section_two:'',
        }
      }
    },
    created(){
      this.$store.state.showHeader = '5';
//      this.$router.push('/set');
      this.id=JSON.parse(localStorage.user).id;
      this.token=JSON.parse(localStorage.user).token;
      this.getUser();
    },
    methods: {
//      获取用户部门
      getUser(){
        let _this=this;
        _this.$axios.post('/api/user-list',{
          "id": _this.id
        })
          .then(function (res) {
//            console.log(res)
            _this.perInfor=res.data.data.data[0];
          })
          .catch(function (error) {
            console.log(error)
          })
      },

//      修改密码
      changePwd(){
        let _this=this;
        _this.$axios.post('/api/password-save',{
          "name":_this.perInfor.name,
          "token":_this.token,
          "password":_this.oldPwd,
          "new_password":_this.newPwd
        })
          .then(function (res) {
//            console.log(res)
            if(res.data.message=='修改成功'){
              _this.$message({
                message: '修改成功',
                type: 'success'
              });
            }
          })
          .catch(function (error) {
            console.log(error)
          })
      },

      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
  }
</script>
<style lang="less" rel="stylesheet/less">
  @import '../../../static/css/base.css';
  .setPosition{
    margin: 0 auto;
    width: 1280px;
    .setTop{
      width: 100%;
      height: 40px;
      line-height: 40px;
      background-color: #f1f1f1;
      margin-bottom: 23px;
      span{
        margin-left: 80px;
        i{
          color:#4a90e2;
          cursor: pointer;
        }
      }
    }
    .setContent{
      // width: 100%;
      height: 100%;
      margin: 0 auto;
      background-color: #f1f1f1;
      .setLeft{
        margin-left: 60px;
        padding-top: 20px;
        width: 900px;
        float: left;
        div{
          margin-top: 20px;
          width: 893px;
          height: 45px;
          line-height: 45px;
          background-color:white;
          margin-bottom: 25px;
          text-indent:25px;
          h1{
            line-height: 45px;
            color: #f5a623;
            text-indent:25px;
          }
          h2{
            line-height: 45px;
            color: #f5a623;
            text-indent:25px;
            margin-top:40px;
          }
          input{
            outline:none;
            float: right;
            width: 554px;
            height: 40px;
          }
        }
        button{
          width: 283.6px;
          height: 42px;
          border-radius: 3px;
          background-color: #4a90e2;
          cursor: pointer;
          color:white;
          margin:148px 0  36px 339px;
          outline:none;
          position: relative;
          top:-40px;
        }
      }
      .setRight{
        margin-right: 50px;
        float: right;
        .topImg{
          width: 250px;
          height: 239px;
          background-color: #ffffff;
          margin: 40px 0 21px 0;
          box-sizing:border-box;
          .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            // width: 178px;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            left: 35px;
            top: 10px;

          }
          .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
          }
          .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 178px;
            height: 178px;
            line-height: 178px;
            text-align: center;
          }
          .avatar {
            width: 178px;
            height: 178px;
            display: block;
          }
          p{
            font-size: 14px;
            text-align: center;
            color: #4a4a4a;
            margin-top: 10px;
          }
        }
        .submit{
          button{
            width: 250px;
            height: 42px;
            border-radius: 3px;
            background-color: #4a90e2;
            outline:none;
            cursor: pointer;
            color:white;
          }
        }
      }
    }
  }
</style>



